                      }
                      &:nth-of-type(4) {
                        .fully-paid-for {
                          .paid-for-container {
                            &__no,
                            &__yes {
                              color: $colorOffWhite;
                            }
                          }
                        }
                      }
                      &:nth-of-type(3),
                      &:nth-of-type(4) {
                        .form-row__section {
                          &:nth-of-type(0),
                          &:nth-of-type(1),
                          &:nth-of-type(2),
                          &:nth-of-type(3),
                          &:nth-of-type(4),
                          &:nth-of-type(6),
                          &:nth-of-type(7),
                          &:nth-of-type(9),
                          &:nth-of-type(10),
                          &:nth-of-type(12),
                          &:nth-of-type(13) {
                            width: 50%;
                            .form-input {
                              width: 90%;
                            }
                          }
                        }
                      }
                    }
                  }
                  .early-income-view {
                    &__investment,
                    &__savings,
                    &__un-allocated {
                      font-size: 3.2rem;
                      color: $primaryColor;
                      &__header {
                        font-size: 1.2rem;
                      }
                    }
                    &__un-allocated {
                      color: $colorOffWhite;
                    }
                  }
                }
                &__transaction-total {
                  font-family: MADE Tommy Soft, Verdana, sans-serif;
                  font-weight: bold;
                  font-size: 6rem;
                  &__heading {
                    font-family: MADE Tommy Soft, Verdana, sans-serif;
                    font-weight: normal;
                    font-size: 2rem;
                  }
