                      &__calendar {
                        &__header {
                          font-size: 2.4rem;
                          color: $primaryColor;
                          &__icon {
                            font-size: 3rem;
                            color: rgba($primaryColor, 0.25);
                            &--not-end {
                              &:hover {
                                color: $primaryColor;
                              }
                            }
                            &:hover {
                              color: $primaryColor;
                            }
                          }
                        }
                        &__weekdays {
                          &__weekday {
                            font-size: 1.6rem;
                          }
                        }
                        &__days {
                          &__single-day {
                            font-size: 2rem;
                            color: rgba($primaryColor, 0.8);
                            &:hover {
                              color: $colorOffWhite;
                            }
                            &--current-day {
                              color: $colorOffWhite;
                            }
                          }
                          .un-used-day {
                            &:hover {
                              background-color: $primaryColor;
                            }
                          }
                        }
                      }
                    }
                    &__upcoming-bills {
                      &__titles {
                        font-family: MADE Tommy Soft, Verdana, sans-serif;
                        color: $colorOffWhite;
                        &__title {
                          font-size: 1.6rem;
                        }
                      }
                      &__bills {
                        font-family: MADE Tommy Soft, Verdana, sans-serif;
                        &__bill {
                          font-size: 1.6rem;
                          color: rgba($primaryColor, 0.8);
