                            text-align: center;
                            &:nth-of-type(5) {
                              input[type='checkbox'] {
                                font-size: 1.2rem;
                                &::after {
                                  font-family: 'Font Awesome 5 Free';
                                  font-weight: 900;
                                }
                                &:checked {
                                  &:after {
                                    color: $colorOffWhite;
                                  }
                                }
                              }
                            }
                          }
                          &:hover {
                            color: $colorOffWhite;
                          }
                        }
                      }
                    }
                    &__calendar-container,
                    &__upcoming-bills {
                    }
                  }
                }
              }
              &--large {
                font-family: MADE Tommy Soft, Verdana, sans-serif;
                &__header {
                  &__title {
                    font-size: 5rem;
                  }
                  &__icon {
                    font-size: 3rem;
                  }
                }
                &__budget-goal-summary-container {
                  .budget-single-goal-summary {
                    &__title {
                      font-size: 1.6rem;
                      color: $primaryColor;
                      text-align: center;
                    }
                    &__amount {
                      font-size: 3rem;
                      color: $primaryColor;
                    }
                  }
                }
                &__category-container {
                  .main-category-display {
                    &__button-container {
                      font-size: 6rem;
                      .left {
                        color: rgba($primaryColor, 0.25);
                      }
                      .right {
                        color: rgba($primaryColor, 0.8);
                      }
                      .left,
                      .right {
                        &:hover {
                          color: $primaryColor;
                        }
                      }
                    }
                    &__category-display {
                      color: $primaryColor;
                      &__icon {
                        font-size: 20rem;
                      }
                      &__title {
                        font-size: 4rem;
                      }
                    }
                  }
                  .sub-category-display {
                    &__headers {
                      &__title-container {
                        font-size: 1.6rem;
                        color: $colorOffWhite;
                        text-align: center;
                      }
                    }
                    &__sub-category {
                      &__section {
                        &__content {
                          font-size: 1.6rem;
                          color: $primaryColor;
                        }
                      }
                      &:hover {
                        .sub-category-display__sub-category__section {
                          &__content {
                            color: $colorOffWhite;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          &__header {
            font-family: MADE Tommy Soft, Verdana, sans-serif;
            font-size: 7rem;
            color: $primaryColor;
          }
          &__budget-management-container {
            font-family: MADE Tommy Soft, Verdana, sans-serif;
            &--extra-small {
              &__header {
                font-size: 2.2rem;
                color: $colorOffWhite;
                &__icon {
                  font-size: 3rem;
                }
                &__text {
                }
              }
              &__budget-name-form,
              &__budget-savings-goal-form,
              &__budget-investment-goal-form,
              &__budget-emergency-goal-form,
              &__budget-tithing-setting-form,
              &__budget-exit-or-delete-form {
                font-family: MADE Tommy Soft, Verdana, sans-serif;
