                  font-family: MADE Tommy Soft, Verdana, sans-serif;
                  font-size: 3rem;
                  color: $primaryColor;
                }
                &__input {
                  font-family: MADE Tommy Soft, Verdana, sans-serif;
                  font-size: 1.6rem;
                  color: rgba($primaryColor, 0.8);
                  &:focus {
                    color: $primaryColor;
                  }
                }
                &__submit,
                &__submit--exit,
                &__submit--delete {
                  font-size: 1.6rem;
                  color: rgba($colorOffWhite, 0.8);
                  &:hover {
                    color: $colorOffWhite;
                  }
                }
              }
              &__budget-savings-goal-form,
              &__budget-investment-goal-form {
                &__input {
                  font-size: 1.6rem;
