                      color: $primaryColor;
                    }
                  }
                  .checked {
                    color: $colorOffWhite;
                  }
                }
                &__selection-container {
                  &__select {
                    color: rgba($colorOffWhite, 0.8);
                    &:hover {
                      color: $colorOffWhite;
                    }
                  }
                  &__input {
                    color: rgba($primaryColor, 0.8);
                    &:hover,
                    &:focus {
                      color: $primaryColor;
                    }
                  }
                  &__input,
                  &__select {
                    font-size: 1.6rem;
                  }
                }
              }
              &__budget-tithing-setting-form {
                &__setting-container {
                  &__label-container {
                    font-size: 2rem;
                    color: rgba($primaryColor, 0.8);
                    &:hover {
                      color: $colorOffWhite;
                    }
                  }
                  .selected {
                    color: rgba($colorOffWhite, 0.8);
                  }
                }
              }
            }
          }
          &__edit-category-goals-container {
            &--large {
              font-family: MADE Tommy Soft, Verdana, sans-serif;
              &__header {
                color: $colorOffWhite;
                &__title {
                  font-size: 5rem;
                }
                &__icon {
                  font-size: 3rem;
                }
              }
              &__budget-goal-summary-container {
                .budget-single-goal-summary {
                  &__title {
                    font-size: 1.6rem;
                    color: $primaryColor;
                    text-align: center;
                  }
                  &__amount {
                    font-size: 3rem;
                    color: $primaryColor;
                  }
                }
              }
              &__category-container {
                .main-category-display {
                  &__button-container {
                    font-size: 6rem;
                    .left {
                      color: rgba($primaryColor, 0.25);
                    }
                    .right {
                      color: rgba($primaryColor, 0.8);
                    }
                    .left,
                    .right {
                      &:hover {
                        color: $primaryColor;
                      }
                    }
                  }
                  &__category-display {
                    color: $primaryColor;
                    &__icon {
                      font-size: 20rem;
                    }
                    &__title {
                      font-size: 4rem;
                    }
                  }
                }
                .sub-category-display {
                  &__headers {
                    &__title-container {
                      font-size: 1.6rem;
                      color: $colorOffWhite;
                      text-align: center;
                    }
                  }
                  &__sub-category {
                    &__section {
                      &__content {
                        font-size: 1.6rem;
                        color: $primaryColor;
                      }
                      &__set-category-timing-button {
