                          &:hover {
                            color: $colorOffWhite;
                          }
                        }
                        &__input {
                          color: rgba($colorOffWhite, 0.8);
                          &:focus {
                            color: $primaryColor;
                          }
                        }
                      }
                    }
                  }
                  &__timing-container {
                    &__close {
                      font-size: 3rem;
                      color: rgba($primaryColor, 0.8);
                      &:hover {
                        color: $primaryColor;
                      }
                    }
                    &__monthly-container,
                    &__bi-monthly-container,
                    &__bi-weekly-container,
                    &__weekly-container {
                      &__button {
                        &__icon {
                          font-family: Icomoon;
                          font-size: 4rem;
                          .path1:before {
                            color: $primaryColor;
                          }
                        }
                        &__text {
                          font-size: 2.2rem;
                          color: rgba($primaryColor, 0.8);
                        }
                        &:hover {
                          .sub-category-display__timing-container {
                            &__monthly-container,
                            &__bi-monthly-container,
                            &__bi-weekly-container,
                            &__weekly-container {
                              &__button {
                                &__icon {
                                  .path1:before {
                                    color: $tertiaryColor;
                                  }
                                  .path2:before,
                                  .path3:before {
                                    color: $colorDarkerGrey;
                                  }
                                }
                                &__text {
                                  color: $colorOffWhite;
                                }
                              }
                            }
                          }
                        }
                      }
                      &__label {
                        font-size: 1.4rem;
                        color: rgba($primaryColor, 0.8);
                        &:focus {
                          color: $primaryColor;
                        }
                        &__input,
                        &__select {
                          font-size: 1.4rem;
                          color: rgba($primaryColor, 0.8);
                          &:focus {
                            color: $primaryColor;
                          }
                        }
                      }
                      &__submit {
                        font-size: 2rem;
                        color: rgba($colorOffWhite, 0.8);
                        &:hover {
                          color: $colorOffWhite;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          &__update-budget-categories-button-container {
            &__button {
              font-size: 2rem;
              color: rgba($primaryColor, 0.8);
              &:hover {
                color: $colorOffWhite;
              }
            }
          }
        }
      }
    }
  }
}
