@use '../../../Public/SCSS/Utilities/Mixins' as *;
@use '../../../Public/SCSS/Utilities/Variables' as *;

////////////////////////////////////////////////
//  Master Grid Container
.master-grid-container {
  .form-container {
    @include SizeAndPositioning(relative, 100%, 100vw, none, row, nowrap, center, center, 0, 0, 0);
    background-color: rgba($colorOffWhite, 0.95);
    &--full-page {
      &:nth-of-type(6) {
        display: flex;
      }
      z-index: 1;
      .form-close-icon {
        @include SizeAndPositioning(absolute, 2rem, 2rem, flex, row, nowrap, center, center, 0, 0, 0);
        top: 5rem;
        right: 6rem;
        &:hover {
          cursor: pointer;
          transition: color 0.5s;
        }
      }
      .login-form,
      .signup-form,
      .forgotten-form,
      .reset-password-form {
        @include SizeAndPositioning(relative, 100%, 50%, flex, column, nowrap, center, center, 0, 0, 0);
        &__header {
          margin-bottom: 3rem;
        }
        &__section {
          @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
          &:not(:last-of-type) {
            margin-bottom: 3.5rem;
          }
          &__input {
            @include SizeAndPositioning(relative, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, 0, 0);
            padding: 1rem;
            padding-left: 2rem;
            background-color: rgba($colorLightestGrey, 0.125);
            border: 0.3rem solid rgba($primaryColor, 0.75);
            border-radius: 0.75rem;
            &::-webkit-input-placeholder,
            &::-moz-placeholder,
            &::placeholder {
              opacity: 0;
            }
            &:focus {
              outline: none;
              &::-moz-placeholder,
              &::placeholder {
                opacity: 1;
              }
            }
            &:focus {
              &::-webkit-input-placeholder {
                opacity: 1;
              }
            }
            &:not(:placeholder-shown) + label,
            &:focus + label {
              transform: translate(-5.5rem, -7rem) scale(0.75);
            }
          }
          &__label {
            @include SizeAndPositioning(absolute, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
            top: 50%;
            left: calc(30% + 2rem);
            -moz-transform: translateY(-50%);
            transition: transform 0.5s;
          }
          &__forgot-username-or-password-link {
            margin-top: 0.75rem;
            &:hover {
              cursor: pointer;
            }
          }
        }
        &__form-page {
          @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
          margin: 0 0 1rem;
          &__section {
            @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
            &:not(:last-of-type) {
              margin-bottom: 3.5rem;
            }
            &__input {
              @include SizeAndPositioning(relative, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, 0, 0);
              padding: 1rem;
              padding-left: 2rem;
              background-color: rgba($colorLightestGrey, 0.125);
              border: 0.3rem solid rgba($primaryColor, 0.75);
              border-radius: 0.75rem;
              &::-webkit-input-placeholder,
              &::-moz-placeholder,
              &::placeholder {
                opacity: 0;
              }
              &:focus {
                &::-webkit-input-placeholder {
                  opacity: 1;
                }
              }
              &:focus {
                outline: none;
                &::-webkit-input-placeholder,
                &::-moz-placeholder,
                &::placeholder {
                  opacity: 1;
                }
              }
              &:not(:placeholder-shown) + label,
              &:focus + label {
                transform: translate(-5.5rem, -7rem) scale(0.75);
              }
            }
            &__label {
              @include SizeAndPositioning(absolute, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
              top: 50%;
              left: calc(30% + 2rem);
              -moz-transform: translateY(-50%);
              transition: transform 0.5s;
            }
            .signup-form__form-page__section__input-container--latter-day-saint {
              @include SizeAndPositioning(relative, 7rem, 20%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
              .signup-form__form-page__section__input--latter-day-saint {
                @include SizeAndPositioning(relative, 95%, 100%, flex, row, nowrap, center, center, -15%, 0, 0);
                background-color: rgba($primaryColorLight, 0.8);
                border: 0.3rem solid rgba($primaryColor, 0.8);
                border-radius: 5rem;
                padding: 0.25rem;
                transition: background-color 0.5s, border 0.5s;
                &__switch {
                  @include SizeAndPositioning(absolute, 84%, 30%, flex, row, nowrap, center, center, 0, -50%, 0);
                  top: 50%;
                  left: 2.75%;
                  background-color: rgba($primaryColorDark, 0.8);
                  border-radius: 5rem;
                  transition: background-color 0.5s, transform 0.5s, width 0.25s;
                }
                &__text--NO {
                  @include SizeAndPositioning(relative, 100%, 70%, flex, row, nowrap, center, center, 0, 0, 0);
                }
                &__text--YES {
                  @include SizeAndPositioning(relative, 100%, 70%, none, row, nowrap, center, center, 0, 0, 0);
                }
                &:hover {
                  cursor: pointer;
                }
                &--switched {
                  border: 0.3rem solid $secondaryColorDark;
                  background-color: $secondaryColorLight;
                  padding: 0.25rem;
                  .signup-form__form-page__section__input--latter-day-saint__switch {
                    width: 94%;
                    background-color: $secondaryColorDark;
                    // transform: translate(215%, -49%);
                    transform: translate(0, -49%);
                  }
                  .signup-form__form-page__section__input--latter-day-saint__text--NO {
                    display: none;
                  }
                  .signup-form__form-page__section__input--latter-day-saint__text--YES {
                    display: flex;
                  }
                }
              }
            }
          }
        }
        .page-section,
        .switch-section {
          flex-direction: row;
          justify-content: center;
          &__page-number {
            margin-right: 9rem;
          }
        }
        .page-section {
          margin-top: 3.5rem;
        }
        .switch-section {
          label {
            position: relative;
            top: 0;
            left: 0;
            transform: translate(10%, 0);
          }
          margin-bottom: 2rem;
        }
        ::-webkit-input-placeholder {
          opacity: 0;
        }
      }
    }
    &--open {
      display: flex;
    }
  }
  .app-flex-container {
    .app-viewport {
      &__display-container {
        .user-profile-container {
          // USER PROFILE FORM CONTAINER
          &__form-container {
            @include SizeAndPositioning(relative, 85%, 100%, flex, column, nowrap, center, center, 0, 0, 0);
            // USER PROFILE FORM
            .user-profile-form {
              @include SizeAndPositioning(relative, 100%, 100%, none, column, nowrap, center, center, 0, 0, 0);
              &__section {
                @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
                &__label {
                  @include SizeAndPositioning(absolute, 7rem, 25%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
                  top: 50%;
                  left: calc(37.7% + 2rem);
                  -moz-transform: translateY(-50%);
                  transition: transform 0.5s;
                }
                &__input {
                  @include SizeAndPositioning(relative, 7rem, 25%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                  padding: 1rem;
                  padding-left: 2rem;
                  background-color: rgba($colorDarkestGrey, 0.5);
                  border: 0.3rem solid rgba($colorOffWhite, 0.75);
                  border-radius: 0.75rem;
                  margin: 2rem 0;
                  &::-webkit-input-placeholder,
                  &::-moz-placeholder,
                  &::placeholder {
                    opacity: 0;
                  }
                  &:focus {
                    &::-webkit-input-placeholder {
                      opacity: 1;
                    }
                  }
                  &:focus {
                    outline: none;
                    &::-webkit-input-placeholder,
                    &::-moz-placeholder,
                    &::placeholder {
                      opacity: 1;
                    }
                  }
                  &:not(:placeholder-shown) + label,
                  &:focus + label {
                    transform: translate(-6rem, -8.25rem) scale(0.75);
                  }
                }
                &__input-container--latter-day-saint {
                  @include SizeAndPositioning(relative, 7rem, 12.5%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
                  .user-profile-form__section__input--latter-day-saint {
                    @include SizeAndPositioning(relative, 95%, 100%, flex, row, nowrap, center, center, -15%, 0, 0);
                    background-color: rgba($primaryColorLight, 0.8);
                    border: 0.3rem solid rgba($primaryColor, 0.8);
                    border-radius: 5rem;
                    padding: 0.25rem;
                    transition: background-color 0.5s, border 0.5s;
                    &__switch {
                      @include SizeAndPositioning(absolute, 84%, 30%, flex, row, nowrap, center, center, 0, -50%, 0);
                      top: 50%;
                      left: 2.75%;
                      background-color: rgba($primaryColorDark, 0.8);
                      border-radius: 5rem;
                      transition: background-color 0.5s, transform 0.5s, width 0.25s;
                    }
                    &__text--NO {
                      @include SizeAndPositioning(relative, 100%, 70%, flex, row, nowrap, center, center, 0, 0, 0);
                    }
                    &__text--YES {
                      @include SizeAndPositioning(relative, 100%, 70%, none, row, nowrap, center, center, 0, 0, 0);
                    }
                    &:hover {
                      cursor: pointer;
                    }
                    &--switched {
                      border: 0.3rem solid $secondaryColorDark;
                      background-color: $secondaryColorLight;
                      padding: 0.25rem;
                      .user-profile-form__section__input--latter-day-saint__switch {
                        width: 94%;
                        background-color: $secondaryColorDark;
                        // transform: translate(215%, -49%);
                        transform: translate(0, -49%);
                      }
                      .user-profile-form__section__input--latter-day-saint__text--NO {
                        display: none;
                      }
                      .user-profile-form__section__input--latter-day-saint__text--YES {
                        display: flex;
                      }
                    }
                  }
                }
                &__sub-section {
                  @include SizeAndPositioning(relative, min-content, 100%, none, column, nowrap, space-evenly, center, 0, 0, 0);
                  &:first-of-type {
                    margin: 0 0 1rem;
                  }
                  margin: 0.5rem 0;
                  &__input {
                    @include SizeAndPositioning(relative, 6rem, 17.5%, flex, row, nowrap, flex-start, center, 15%, 0, 0);
                    padding: 1rem;
                    padding-left: 2rem;
                    background-color: rgba($colorDarkestGrey, 0.5);
                    border: 0.3rem solid rgba($colorOffWhite, 0.75);
                    border-radius: 0.75rem;
                    margin: 0.5rem 0;
                    &::-webkit-input-placeholder,
                    &::-moz-placeholder,
                    &::placeholder {
                      opacity: 0;
                    }
                    &:focus {
                      outline: none;
                      &::-webkit-input-placeholder,
                      &::-moz-placeholder,
                      &::placeholder {
                        opacity: 1;
                      }
                    }
                    &:not(:placeholder-shown) + label,
                    &:focus + label {
                      transform: translate(-4rem, -7rem) scale(0.75);
                    }
                  }
                  &__label {
                    @include SizeAndPositioning(absolute, 6rem, 17.5%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
                    top: 50%;
                    left: calc(43.75% + 2rem);
                    -moz-transform: translateY(-50%);
                    transition: transform 0.5s;
                  }
                  &--show {
                    display: flex;
                    .user-profile-form__section__label {
                      transform: translate(-4rem, -16.25rem) scale(0.75);
                    }
                  }
                }
                &__comm-switch {
                  @include SizeAndPositioning(relative, 100%, 15%, flex, row, nowrap, center, center, 0, 0, 0);
                  background-color: rgba($colorDarkestGrey, 0.5);
                  border: 0.4rem solid rgba($colorOffWhite, 0.8);
                  border-radius: 8rem;
                  padding: 0.5rem;
                  &--email,
                  &--text {
                    @include SizeAndPositioning(relative, 100%, 50%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
                    z-index: 1;
                  }
                  &--email {
                    border-top-left-radius: 8rem;
                    border-bottom-left-radius: 8rem;
                  }
                  &--text {
                    border-top-right-radius: 8rem;
                    border-bottom-right-radius: 8rem;
                  }
                  &:after {
                    @include SizeAndPositioning(absolute, 85%, 50%, flex, row, nowrap, center, center, 0, -50%, 0);
                    top: 50%;
                    left: 0.5rem;
                    background-color: $secondaryColor;
                    border-top-left-radius: 8rem;
                    border-bottom-left-radius: 8rem;
                    z-index: 0;
                    content: '';
                    transition: transform 0.5s, border-radius 0.5s;
                  }
                  &--text-preferred {
                    &:after {
                      @include SizeAndPositioning(absolute, 85%, 50%, flex, row, nowrap, center, center, 91%, -50%, 0);
                      top: 50%;
                      left: 0.5rem;
                      background-color: $secondaryColor;
                      border-top-left-radius: 0;
                      border-bottom-left-radius: 0;
                      border-top-right-radius: 8rem;
                      border-bottom-right-radius: 8rem;
                      z-index: 0;
                      content: '';
                    }
                  }
                  &:hover {
                    cursor: pointer;
                  }
                }
              }
              .switch-section {
                flex-direction: row;
                justify-content: center;
                margin-top: 2rem;
                label {
                  position: relative;
                  top: 0;
                  left: 0;
                  width: 14%;
                  transform: translate(0, 0);
                }
                .comms-label {
                  width: 18%;
                }
                margin-bottom: 2rem;
              }
              ::-webkit-input-placeholder {
                opacity: 0;
              }
            }
          }
        }
        .budget-creation-form-container {
          .form-close-icon,
          .budget-creation-form-close-icon {
            @include SizeAndPositioning(absolute, 2rem, 2rem, flex, row, nowrap, center, center, 0, 0, 0);
            top: 5rem;
            right: 6rem;
            z-index: 10;
            &:hover {
              cursor: pointer;
              transition: color 0.5s;
            }
          }
          .budget-creation-form {
            @include SizeAndPositioning(relative, 100%, 100%, flex, column, nowrap, center, center, 0, 0, 0);
            ::-webkit-input-placeholder {
              opacity: 0;
            }
            &__header {
              @include SizeAndPositioning(relative, 15%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
            }
            &__page {
              @include SizeAndPositioning(relative, 70%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
              &:nth-of-type(1),
              &:nth-of-type(5),
              &:nth-of-type(6),
              &:nth-of-type(7) {
                flex-direction: column;
                & > .budget-creation-form__page__section {
                  & > .budget-creation-form__page__section__input {
                    width: 25%;
                    &:not(:placeholder-shown) + label,
                    &:focus + label {
                      transform: translate(-5rem, -7rem) scale(0.75);
                    }
                  }
                  & > .budget-creation-form__page__section__label {
                    width: 25%;
                    left: calc(30% + 12.5rem);
                  }
                }
              }
              &:nth-of-type(6),
              &:nth-of-type(7) {
                & > .budget-creation-form__page__section {
                  & > .savings-percent-goal,
                  & > .investment-percent-goal {
                    &:first-of-type {
                      margin-bottom: 2.4rem;
                      & + label {
                        top: 21%;
                      }
                    }
                  }
                  .budget-creation-form__page__section__input {
                    &:last-of-type {
                      & + label {
                        top: 79%;
                      }
                      & + .emergency-checkbox-label,
                      & + .emergency-goal-label {
                        top: 50%;
                      }
                    }
                  }
                }
              }
              &:nth-of-type(8) {
                flex-direction: column;
                & > .budget-creation-form__page__section {
                  & > .investment-percent-goal {
                    margin-bottom: 2.4rem;
                    &:first-of-type {
                      margin-bottom: 2.4rem;
                      & + label {
                        top: 21%;
                      }
                    }
                  }
                  & > .budget-creation-form__page__section__input {
                    width: 25%;
                    &:last-of-type {
                      & + label {
                        top: 79%;
                      }
                    }
                    &:not(:placeholder-shown) + label,
                    &:focus + label {
                      transform: translate(-5rem, -7rem) scale(0.75);
                    }
                  }
                  & > .budget-creation-form__page__section__label {
                    width: 25%;
                    left: calc(30% + 12.5rem);
                  }
                }
              }
              &__section {
                @include SizeAndPositioning(relative, min-content, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
                &:not(:last-of-type) {
                  margin-bottom: 3.5rem;
                }
                &__select {
                  @include SizeAndPositioning(relative, 6rem, 12.5%, flex, row, nowrap, center, center, 0, 0, 0);
                  background-color: rgba($primaryColor, 0.8);
                  border: 0.3rem solid rgba($primaryColor, 0.8);
                  border-top-right-radius: 0.9rem;
                  border-bottom-right-radius: 0.9rem;
                  &:focus {
                    background-color: $primaryColor;
                    border: 0.3rem solid $primaryColor;
                  }
                  option,
                  &__option {
                    background-color: $primaryColor;
                    &:hover {
                      background-color: $secondaryColor;
                    }
                  }
                }
                &__input {
                  @include SizeAndPositioning(relative, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                  padding: 1rem;
                  padding-left: 2rem;
                  background-color: rgba($colorLightestGrey, 0.125);
                  border: 0.3rem solid rgba($primaryColor, 0.75);
                  border-radius: 0.75rem;
                  &::-webkit-input-placeholder,
                  &::-moz-placeholder,
                  &::placeholder {
                    opacity: 0;
                  }
                  &:focus {
                    &::-webkit-input-placeholder {
                      opacity: 1;
                    }
                  }
                  &:focus {
                    outline: none;
                    &::-webkit-input-placeholder,
                    &::-moz-placeholder,
                    &::placeholder {
                      opacity: 1;
                    }
                  }
                  &:not(:placeholder-shown) + label,
                  &:focus + label {
                    transform: translate(-8.5rem, -7rem) scale(0.75);
                  }
                }
                &__label {
                  @include SizeAndPositioning(absolute, 6rem, 40%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
                  top: 50%;
                  left: calc(30% + 2rem);
                  -moz-transform: translateY(-50%);
                  transition: transform 0.5s;
                }
                &__main-category-container {
                  @include SizeAndPositioning(relative, 50rem, 40%, flex, row, nowrap, flex-start, flex-start, 0, 0, 0);
                  border: 0.3rem solid $primaryColor;
                  border-radius: 1.25rem;
                  flex-flow: row wrap;
                  overflow-y: auto;
                  &__create-main-category {
                    @include SizeAndPositioning(relative, 25%, 25%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
                    border: 0.3rem solid rgba($primaryColor, 0.8);
                    border-top-left-radius: 0.9rem;
                    &:hover {
                      cursor: pointer;
                      border: 0.3rem solid $primaryColor;
                      background-color: $primaryColor;
                      transition: border 0.5s, background-color 0.5s;
                      .budget-creation-form__page__section__main-category-container__create-main-category__icon,
                      .budget-creation-form__page__section__main-category-container__create-main-category__text {
                        transition: color 0.5s;
                      }
                    }
                    &__icons-container {
                      @include SizeAndPositioning(relative, 80%, 100%, none, row, wrap, center, center, 0, 0, 0);
                      border: 0.3rem solid $primaryColor;
                      grid-template-columns: repeat(3, 1fr);
                      border-top-left-radius: 0.9rem;
                      border-top-right-radius: 0.9rem;
                      overflow-y: auto;
                      .icon-container {
                        @include SizeAndPositioning(relative, 100%, 100%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
                        padding: 3rem 0;
                        &:first-of-type {
                          border-top-left-radius: 0.5rem;
                        }
                        z-index: 3;
                        &:hover {
                          cursor: pointer;
                          background-color: $primaryColor;
                          transition: background-color 0.5s, color 0.5s;
                        }
                        &__icon {
                          position: relative;
                          z-index: 0;
                          margin-bottom: 0.5rem;
                        }
                        &--clicked {
                          background-color: $primaryColor;
                        }
                      }
                      &--open {
                        display: grid;
                      }
                    }
                    &--hidden {
                      display: none;
                    }
                  }
                  .main-category {
                    @include SizeAndPositioning(relative, 25%, 25%, flex, column, nowrap, space-evenly, center, 0, 0, 0);
                    border: 0.3rem solid rgba($primaryColor, 0.8);
                    &:first-of-type {
                      border-top-left-radius: 0.9rem;
                    }
                    &:nth-of-type(3) {
                      border-top-right-radius: 0.9rem;
                    }
                    &--hidden {
                      display: none;
                    }
                    &:hover {
                      cursor: pointer;
                      border: 0.3rem solid $primaryColor;
                      background-color: $primaryColor;
                      transition: border 0.5s, background-color 0.5s;
                      .budget-creation-form__page__section__main-category-container__create-main-category__icon,
                      .budget-creation-form__page__section__main-category-container__create-main-category__text {
                        transition: color 0.5s;
                      }
                    }
                  }
                  &--creating-category {
                    flex-direction: column;
                  }
                }
                &__set-main-category-title-container {
                  @include SizeAndPositioning(relative, 20%, 100%, none, row, nowrap, space-evenly, center, 0, 0, 0);
                  border-top: 0.3rem solid $primaryColor;
                  &__input {
                    @include SizeAndPositioning(relative, 5rem, 35%, flex, row, nowrap, center, center, 0, 0, 0);
                    border: 0.3rem solid rgba($primaryColor, 0.8);
                    border-radius: 1.25rem;
                    background-color: rgba($colorOffWhite, 0.8);
                    padding: 1rem;
                    &:focus {
                      outline: none;
                    }
                  }
                  &--open {
                    display: flex;
                  }
                }
                &__sub-category-container {
                  @include SizeAndPositioning(relative, 50rem, 35%, flex, column, nowrap, center, center, 0, 0, 0);
                  border: 0.3rem solid $primaryColor;
                  border-radius: 1rem;
                  &__main-category-display {
                    @include SizeAndPositioning(relative, 55%, 100%, flex, row, nowrap, center, center, 0, 0, 0);
                    &__left-button,
                    &__right-button {
                      @include SizeAndPositioning(relative, 100%, 20%, flex, row, nowrap, center, center, 0, 0, 0);
                      &__icon {
                        &:hover {
                          cursor: pointer;
                          transition: color 0.5s;
                        }
                      }
                    }
                    &__category-information {
                      @include SizeAndPositioning(relative, 100%, 60%, flex, column, nowrap, center, center, 0, 0, 0);
                    }
                  }
                  &__sub-category-display {
                    @include SizeAndPositioning(relative, 45%, 100%, flex, column, nowrap, flex-start, center, 0, 0, 0);
                    border-top: 0.3rem solid $primaryColor;
                    overflow-y: auto;
                    &__sub-category {
                      @include SizeAndPositioning(relative, 6rem, 100%, flex, row, nowrap, center, center, 0, 0, 0);
                      min-height: 6rem;
                      border-bottom: 0.3rem solid rgba($primaryColor, 0.8);
                      border-left: none;
                      border-right: none;
                      background-color: transparent;
                      .sub-category-title-container {
                        @include SizeAndPositioning(relative, 100%, 70%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                        padding-left: 2rem;
                      }
                      .sub-category-controller {
                        @include SizeAndPositioning(relative, 100%, 30%, flex, row, nowrap, space-evenly, center, 0, 0, 0);
                        &__surplus-container {
                          @include SizeAndPositioning(relative, 100%, 50%, flex, column, nowrap, center, center, 0, 0, 0);
                          &__switch {
                            @include SizeAndPositioning(relative, 40%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                            border: 0.2rem solid rgba($primaryColorDark, 0.8);
