                  outline: none;
                  border: 0.2rem solid $primaryColor;
                  background-color: rgba($colorOffWhite, 0.5);
                  transition: color 0.5s, border 0.5s, background-color 0.5s;
                }
              }
              &__submit,
              &__submit--exit,
              &__submit--delete {
                @include SizeAndPositioning(relative, 20%, 30%, flex, column, nowrap, center, center, -4.25rem, 0.75rem, 0);
                align-self: flex-end;
                background-color: rgba($primaryColor, 0.5);
                border: 0.2rem solid rgba($primaryColor, 0.8);
                border-radius: 0.5rem;
                min-height: 3.24rem;
                &:hover {
                  cursor: pointer;
                  background-color: $primaryColor;
                  border: 0.2rem solid $primaryColor;
                  transition: background-color 0.5s, color 0.5s, border 0.5s;
                }
              }
              &__submit--exit,
              &__submit--delete {
                transform: translate(0, 0);
                width: 40%;
                align-self: center;
                &:first-of-type {
                  margin-bottom: 1rem;
                }
              }
            }
            &__budget-savings-goal-form,
            &__budget-investment-goal-form {
              &__input {
                &::-webkit-input-placeholder,
                &::-moz-placeholder,
                &::placeholder {
                  opacity: 0;
                }
                &:focus {
                  outline: none;
                  &::-moz-placeholder,
                  &::placeholder {
                    opacity: 1;
                  }
                }
                &:focus {
                  &::-webkit-input-placeholder {
                    opacity: 1;
                  }
                }
                & + label {
                  @include SizeAndPositioning(absolute, 20%, 70%, flex, row, nowrap, flex-start, center, 0, -50%, 0);
                  top: 56%;
                  left: 6rem;
                  &:first-of-type {
                    top: 24%;
                  }
                  transition: transform 0.5s;
                }
                &:not(:placeholder-shown) + label,
                &:focus + label {
                  transform: translate(-3rem, -4.25rem) scale(0.75);
                }
                &:nth-of-type(1) {
                  margin-bottom: 2rem;
                }
              }
            }
            &__budget-emergency-goal-form {
              justify-content: flex-start;
              &__label--checkbox-outer {
                @include SizeAndPositioning(relative, 20%, 75%, flex, row, nowrap, flex-start, center, 0, 0, 0);
                padding-left: 1.5rem;
                min-height: 3.24rem;
              }
              &__label-container {
                @include SizeAndPositioning(relative, 30%, 70%, flex, row, nowrap, center, center, 0, 0, 0);
                min-height: 4.725rem;
                &--checkbox-container {
                  @include SizeAndPositioning(relative, 100%, 50%, flex, row, nowrap, center, center, 0, 0, 0);
                  border-top: 0.2rem solid $primaryColor;
                  border-bottom: 0.2rem solid $primaryColor;
                  margin-bottom: 1rem;
                  transition: background-color 0.5s, color 0.5s;
                  &:nth-of-type(1) {
                    border-top-left-radius: 0.5rem;
                    border-bottom-left-radius: 0.5rem;
                    border-left: 0.2rem solid $primaryColor;
                  }
                  &:nth-of-type(2) {
                    border-top-right-radius: 0.5rem;
                    border-bottom-right-radius: 0.5rem;
                    border-right: 0.2rem solid $primaryColor;
                  }
                  &__input--checkbox {
                    appearance: none;
                    -webkit-appearance: none;
                    display: none;
                  }
                  &:hover {
                    cursor: pointer;
                  }
                }
                .checked {
                  background-color: $primaryColor;
                }
              }
              &__selection-container {
                @include SizeAndPositioning(relative, 20%, 70%, none, row, nowrap, center, center, 0, 0, 0);
                min-height: 3.24rem;
                &__select {
                  border-top-right-radius: 0.5rem;
                  border-bottom-right-radius: 0.5rem;
                  border-right: 0.2rem solid $primaryColor;
                  border-left: none;
                  background-color: rgba($primaryColor, 0.8);
                  &:hover {
                    background-color: $primaryColor;
                  }
                }
                &__input {
                  border-top-left-radius: 0.5rem;
                  border-bottom-left-radius: 0.5rem;
                  border-left: 0.2rem solid $primaryColor;
                  background-color: transparent;
                  &:focus {
                    background-color: rgba($colorOffWhite, 0.25);
                  }
                }
                &__select,
                &__input {
                  @include SizeAndPositioning(relative, 100%, 50%, flex, row, nowrap, center, center, 0, 0, 0);
                  border-top: 0.2rem solid $primaryColor;
                  border-bottom: 0.2rem solid $primaryColor;
                  padding-left: 1rem;
                  transition: background-color 0.5s, color 0.5s, border 0.5s;
                  &:focus {
                    outline: none;
                  }
                  &:hover {
                    cursor: pointer;
                  }
                }
              }
              &__input {
                display: none;
              }
              .visible {
                display: flex;
              }
            }
            &__budget-tithing-setting-form {
              &__setting-container {
                @include SizeAndPositioning(relative, 40%, 70%, flex, row, nowrap, center, center, 0, 0, 0);
                &__label-container {
                  @include SizeAndPositioning(relative, 100%, 33.33%, flex, row, nowrap, center, center, 0, 0, 0);
                  &__input--checkbox {
                    appearance: none;
                    -webkit-appearance: none;
                    display: none;
                  }

                  &:first-of-type {
                    border-top: 0.2rem solid $primaryColor;
                    border-left: 0.2rem solid $primaryColor;
                    border-bottom: 0.2rem solid $primaryColor;
                    border-top-left-radius: 0.5rem;
                    border-bottom-left-radius: 0.5rem;
                  }
                  &:nth-of-type(2) {
                    border-top: 0.2rem solid $primaryColor;
                    border-bottom: 0.2rem solid $primaryColor;
                  }
                  &:nth-of-type(3) {
                    border-top: 0.2rem solid $primaryColor;
                    border-right: 0.2rem solid $primaryColor;
                    border-bottom: 0.2rem solid $primaryColor;
                    border-top-right-radius: 0.5rem;
                    border-bottom-right-radius: 0.5rem;
                  }
                  &:hover {
                    cursor: pointer;
                    background-color: $primaryColor;
                    transition: background-color 0.5s, border 0.5s, color 0.5s;
                  }
                }
                .selected {
                  background-color: rgba($primaryColor, 0.8);
                }
              }
            }
          }
        }
      }
    }
  }
}
